import React from "react";
import { Link } from "react-router-dom";
import avatar from "../../images/avatar.jpg";

import { Link as LinkScroll, animateScroll as scroll } from "react-scroll";


const Header = () => {
    return (
        <>
                <div id="header">

                    <div className="top">

                        <div id="logo">
                            
                            {/*<span className="image avatar48"><img src={avatar} alt="" /></span>*/}
                            <Link to="/react-gh-pages/">
                                <h1 id="title">Tristan Johnson</h1>
                            </Link>
                            <p>Software Developer</p>

                        </div>



                        <nav id="nav">

                            <ul>
                                <li>
                                    <LinkScroll
                                        activeClass="active"
                                        to="top"
                                        spy={true}
                                        smooth={true}
                                        offset={0}
                                        duration={500}
                                    >
                                        <span className="icon solid fa-home">Intro</span>
                                    </LinkScroll>
                                </li>

                                <li>
                                    <LinkScroll
                                        activeClass="active"
                                        to="portfolio"
                                        spy={true}
                                        smooth={true}
                                        offset={0}
                                        duration={500}
                                    >
                                        <span className="icon solid fa-th">Portfolio</span>
                                    </LinkScroll>
                                </li>

                                <li>
                                    <LinkScroll
                                        activeClass="active"
                                        to="about"
                                        spy={true}
                                        smooth={true}
                                        offset={0}
                                        duration={500}
                                    >
                                        <span className="icon solid fa-user">About Me</span>
                                    </LinkScroll>
                                </li>

                                <li>
                                    <LinkScroll
                                        activeClass="active"
                                        to="contact"
                                        spy={true}
                                        smooth={true}
                                        offset={0}
                                        duration={500}
                                    >
                                        <span className="icon solid fa-envelope">Contact</span>
                                    </LinkScroll>
                                </li>
                            </ul>
                        </nav>

                    </div>

                    <div className="bottom">

                        <ul className="icons">
                            {/*<li><a href="#" className="icon brands fa-twitter"><span className="label">Twitter</span></a></li>*/}
                            {/*<li><a href="#" className="icon brands fa-facebook-f"><span className="label">Facebook</span></a></li>*/}
                            <li><a href="https://github.com/Tristan367" className="icon brands fa-github"><span className="label">Github</span></a></li>
                            {/*<li><a href="#" className="icon brands fa-dribbble"><span className="label">Dribbble</span></a></li>*/}
                            {/*<li><a href="#" className="icon solid fa-envelope"><span className="label">Email</span></a></li>*/}
                        </ul>

                    </div>

                </div>

                <div className = "headerBottomPadding"></div>



        </>
    )
}

export default Header;