import React from "react";
import {Route, Switch} from "react-router-dom";

import './App.css';
import './assets/css/main.css'

//import './assets/js/jquery.min.js'
//import './assets/js/jquery.scrolly.min.js'
//import './assets/js/jquery.scrollex.min.js'
//import './assets/js/browser.min.js'
//import './assets/js/breakpoints.min.js'
//import './assets/js/util.js'
//import './assets/js/main.js'

import HomePage from "./components/home/HomePage";
import Project1 from "./components/project1/Project1";

//import Header from "./components/shared/Header"
import Footer from "./components/shared/Footer"
import Project2 from "./components/project2/Project2";
import Project3 from "./components/project3/Project3";
import Project4 from "./components/project4/Project4";



const App = () => {

    return(
        <>
        <Switch>

          <Route path = "/react-gh-pages/" exact component = {HomePage}/>
          <Route path = "/react-gh-pages/project1" component = {Project1}/>
          <Route path = "/react-gh-pages/project2" component = {Project2}/>
          <Route path = "/react-gh-pages/project3" component = {Project3}/>
          <Route path = "/react-gh-pages/project4" component = {Project4}/>

        </Switch>
        <Footer/>
        
        </>
    )

}

export default App;
