import React from "react";
import { Link } from "react-router-dom";
import avatar from "../../images/avatar.jpg";
import { Link as LinkScroll, animateScroll as scroll } from "react-scroll";

import Header from "../shared/Header2";

import GoToTop from '../shared/GoToTop.js';

import voxels1 from "./voxel3DNoise1.PNG";
import voxels2 from "./voxel3DNoise2.PNG";
import voxels3 from "./VoxelRasterizer3.PNG";


import chunkScript from "./code/ChunkWithMesh.cs"
import chunksManagerScript from "./code/ChunksManager.cs"
import noiseComputeShader from "./code/NoiseComputeShader.compute"



const Project1 = () => {
    return (
        <>

            <div className="is-preload">

                <Header/>

                <div id="main">



                    <section id="portfolio" className="two">
                        <div className="container">

                            <header>
                                <h2>GPU Calculated Noise with Multi-threaded Mesh Generation in Unity</h2>
                            </header>

                            <p>This project was produced with C# and HLSL. The terrain is infinite in all directions and generates in chunks, which are computed in parrelell on the GPU. Pointers to voxel data are stored in a hash table and meshes are calculated on another thread. I'm using the open source library, FastNoiseLite. I have included some of the source code below the images, check it out!.  </p>

                        </div>
                    </section>

                    <img src={voxels1} className = "center" alt="voxel 3D noise" />
                    <img src={voxels2} className = "center" alt="voxel 3D noise" />
                    <img src={voxels3} className = "center" alt="voxel 3D noise" />


                    <div>
                        <a href={chunksManagerScript}> Chunk Management </a>
                        <br />
                        <a href={noiseComputeShader}> Compute Shader </a>
                        <br />
                        <a href={chunkScript}> Chunk class </a>
                    </div>

                </div>

            </div>

            <GoToTop/>

        </>
    )
}

export default Project1;