import React from "react";
//import { Link } from "react-router-dom";
import { Link, animateScroll as scroll } from "react-scroll";

const Footer = () => {


    return (
        <>

                <div id="footer">

                    <div className = "tinyBottomPadding">
                        <button type="button" onClick={scroll.scrollToTop}>return to top</button>
                    </div>
                    <ul className="copyright">
                        <li> Tristan Johnson</li>
                        <li>&copy;2021</li>
                    </ul>

                </div>



        </>
    )
}

export default Footer;