import React from "react";
import { Link } from "react-router-dom";
import avatar from "../../images/avatar.jpg";
import { Link as LinkScroll, animateScroll as scroll } from "react-scroll";
import Header from "../shared/Header2";


import GoToTop from '../shared/GoToTop.js';

import voxelRayCasting1 from "./VoxelRaycasting1.PNG";
import voxelRayCasting2 from "./VoxelRaycasting2.PNG";
import voxelRayCasting3 from "./VoxelRaycasting3.PNG";

import rayCastingShader from "./RayTracing3.compute"

const Project3 = () => {
    return (
        <>
            <div className="is-preload">

                <Header/>

                <div id="main">



                    <section id="portfolio" className="two">
                        <div className="container">

                            <header>
                                <h2>Raycasting Voxels in a Compute Shader</h2>
                            </header>

                            <p>In this project I am using Unity to render to a texture, and computing the pixels by marching rays along a grid of integer points in an HLSL compute shader.</p>

                        </div>
                    </section>


                    <div>
                            <img src = {voxelRayCasting1} className = "center" alt="raycasting voxels"/>
                            <img src = {voxelRayCasting2} className = "center" alt="raycasting voxels"/>
                            <img src = {voxelRayCasting3} className = "center" alt="raycasting voxels"/>
                    </div>

                    <a href = {rayCastingShader}>The Raycasting Compute Shader</a>

                </div>

            </div>
            <GoToTop/>

        </>
    )
}

export default Project3;